/* eslint-disable jsx-a11y/control-has-associated-label */
import React, { ChangeEvent } from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { actions, actions as actionsFilter } from '../../features/filter';
import { Status } from '../../types/Status';

export const TodoFilter: React.FC = () => {
  const dispatch = useAppDispatch();

  const query = useAppSelector(state => state.filter.query);

  const handleTodosFilter = (event: ChangeEvent<HTMLSelectElement>) => {
    dispatch(actionsFilter.setStatusFilter(event.target.value as Status));
  };

  const handleInputQuery = (event: ChangeEvent<HTMLInputElement>) => {
    dispatch(actions.setQueryFilter(event.target.value));
  };

  const clearSearchQuery = () => {
    dispatch(actions.clearQueryFilter());
  };

  return (
    <form
      className="field has-addons"
      onSubmit={event => event.preventDefault()}
    >
      <p className="control">
        <span className="select">
          <select
            data-cy="statusSelect"
            onChange={handleTodosFilter}
          >
            <option value="all">All</option>
            <option value="active">Active</option>
            <option value="completed">Completed</option>
          </select>
        </span>
      </p>

      <p className="control is-expanded has-icons-left has-icons-right">
        <input
          data-cy="searchInput"
          type="text"
          className="input"
          placeholder="Search..."
          value={query}
          onChange={handleInputQuery}
        />
        <span className="icon is-left">
          <i className="fas fa-magnifying-glass" />
        </span>

        <span className="icon is-right" style={{ pointerEvents: 'all' }}>
          {query && (
            <button
              data-cy="clearSearchButton"
              type="button"
              className="delete"
              onClick={clearSearchQuery}
            />
          )}

        </span>
      </p>
    </form>
  );
};
